.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 8%;
  background-image: url("../../images/bkgfrontpage.svg");
  background-size: 110%;
  background-position: top;
  background-repeat: no-repeat;
}
body main {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  max-width: 500px;
  grid-column: 1/-1;
  justify-self: center;
}
body main #DotHolder {
  width: 30%;
  max-width: 100px;
  display: flex;
  justify-self: center;
  justify-content: space-evenly;
}
body main img {
  width: 20%;
  margin: 33px 0 24px 0;
}
body main .Dot {
  outline: none;
  width: 10px;
  height: 10px;
  border: 1.5px solid #313131;
  border-radius: 100%;
  padding: 0;
  background-color: #FFFFFF;
}
body main .ActiveDot {
  background-color: #005140;
  border: none;
}
body main form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-column: 1/-1;
  justify-items: center;
}
body main form label {
  justify-self: start;
  margin-bottom: 1px;
  font-size: 20px;
  font-family: "Raleway";
}
body main form .Formula {
  display: none;
}
body main form .Formula .ButtonContainer {
  display: flex;
  justify-content: end;
  flex-wrap: wrap;
}
body main form .Formula #SecondContainer {
  justify-content: space-between;
}
body main form .Formula #ThirdContainer {
  justify-content: start;
}
body main form .Displayed {
  display: block;
}
body main form input, body main form select {
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 21px;
  padding: 6px 0;
  border: solid 2px #005140;
  border-radius: 0.4em;
  text-indent: 5px;
}
body main form .WideButton {
  width: 100%;
  padding: 11px 0;
  box-shadow: rgba(0, 0, 0, 0.16) 0 3px 6px;
  margin-bottom: 18px;
  font-size: 20px;
  font-family: "Asap";
  font-weight: normal;
}